.active:link,
.active:visited {
  color: var(--color-white);
  transition: 0.5s ease;
}
.active:hover,
.active:active {
  color: var(--colour-grey);
}

@media screen and (max-width: 1550px) {
  .logout-button {
    font-size: 2rem;
  }

  .li {
    font-size: 2rem;
  }
}
@media screen and (max-width: 650px) {
  .logout-button {
    font-size: 2rem;
  }
}
