.active:link,
.active:visited {
  color: var(--color-white);
  transition: 0.5s ease;
}
.active:hover,
.active:active {
  color: var(--colour-grey);
}

.logout-button {
  background: var(--color-grey);
  color: var(--color-white);
  font-family: var(--font-alt);
  font-weight: 400;
  letter-spacing: 0.2rem;
  line-height: 28px;
  font-size: 16px;
  padding: 0.5rem 1.5rem;
  border-radius: 1px;
  border: none;
  outline: none;
  cursor: pointer;
}

.logout-button:hover {
  background: var(--color-golden);
}

@media screen and (min-width: 2000px) {
  .logout-button {
    font-size: 3rem;
    padding: 1rem;
  }
}
@media screen and (max-width: 1550px) {
  .logout-button {
    font-size: 2rem;
  }

  .li {
    font-size: 2rem;
  }
}
@media screen and (max-width: 650px) {
  .logout-button {
    font-size: 2rem;
  }
}
