.footer {
  width: 100%;
  position: relative;
  z-index: 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  background: var(--color-black);
  padding-top: 0;
}

.footer-links {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-top: 5rem;
  padding: 0 2rem;
}
.footer-links-contact,
.footer-links-logo,
.footer-links-work {
  flex: 1;
  text-align: center;
  margin: 1rem;
}

.footer-links-logo img:first-child {
  width: 21rem;
  margin-bottom: 0.75rem;
}
.footer-links-work p:nth-child(2n + 1) {
  margin-bottom: 1rem;
}

.footer-copyright {
  margin-top: 3rem;
}
.footer-headtext {
  font-weight: 400;
  text-transform: capitalize;
  letter-spacing: 0.2rem;
  font-size: 3.2rem;
  line-height: 4.1rem;
}

.links-icons {
  margin-top: 0.5rem;
}
.links-icons svg {
  color: var(--color-white);
  margin: 0.5rem;
  font-size: 2.4rem;
  cursor: pointer;
}

.links-icons svg:hover {
  color: var(--color-golden);
}

@media screen and (min-width: 2000px) {
  .footer-headtext {
    font-size: 5.1rem;
    line-height: 6.1rem;
  }
}

@media screen and (max-width: 1150px) {
  .footer-links {
    align-items: center;
    flex-direction: column;
    padding: 0;
  }
  .footer-links-contact,
  .footer-links-logo,
  .footer-links-work {
    margin: 2rem 0;
    width: 100%;
  }
}
@media screen and (max-width: 650px) {
  .footer {
    padding: 0 0 2rem 0;
  }
}
@media screen and (max-width: 350px) {
  .footer-links-logo img:first-child {
    width: 80%;
  }
}
