.newsletter {
  padding: 2rem 4rem;
  border: 1px solid var(--color-golden);
  background: var(--color-black);
}

.newletter-heading {
  text-align: center;
}
.newsletter-input {
  flex-direction: row;
  margin-top: 3rem;
}

.newsletter-input input {
  width: 62rem;
  border: 1px solid var(--color-golden);
  border-radius: 2px;
  font-size: 1rem;
  font-family: var(--font-base);
  color: var(--color-white);

  margin-right: 2rem;
  padding: 0.75rem 1rem;
  background: var(--color-black);
}

.newsletter-input button {
  width: max-content;
}

@media screen and (min-width: 2000px) {
  .newsletter-input input {
    font-size: 2rem;
    width: 100%;
  }
}

@media screen and (max-width: 990px) {
  .newsletter-input {
    flex-direction: column;
    width: 100%;
  }

  .newsletter-input input {
    margin: 0 0 2rem 0;
    width: 90%;
  }
}
@media screen and (max-width: 650px) {
  .newsletter {
    border: none;
    padding: 2rem 0;
  }
}
@media screen and (max-width: 300px) {
  .newletter-heading h1 {
    font-size: 3.2rem;
    line-height: 5rem;
  }
}
